    "Acne":"Acne is a skin condition that occurs when your hair follicles become plugged with oil and dead skin cells. It causes whiteheads, blackheads or pimples. Acne is most common among teenagers, though it affects people of all ages.",
    "Urinary tract infection":"Urinary tract infections are more common in women. They usually occur in the bladder or urethra, but more serious infections involve the kidney.\
    A bladder infection may cause pelvic pain, increased urge to urinate, pain with urination and blood in the urine. A kidney infection may cause back pain, nausea, vomiting and fever.\
    Common treatment is with antibiotics.",
    "Psoriasis":"Psoriasis is thought to be an immune system problem. Triggers include infections, stress and cold.\
    The most common symptom is a rash on the skin, but sometimes the rash involves the nails or joints.\
    Treatment aims to remove scales and stop skin cells from growing so quickly. Topical ointments, light therapy and medication can offer relief.",
    "Impetigo":"Impetigo mainly affects infants and children.\
    The main symptom is red sores that form around the nose and mouth. The sores rupture, ooze for a few days, then form a yellow-brown crust.\
    Antibiotics shorten the infection and can help prevent spread to others.",
